.main {
  height: 97vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-image: url(../images/we-love-resp.jpg);
  &__title {
    color: white;
    font-size: 40px;
  }
  &__form {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
.main__form--label {
  color: white;
  font-size: 18px;
}
.main__form--input {
  margin: 10px 0;
}
